import React from 'react';
import { Form, Input, Modal, Button, FormInstance } from 'antd';

import styles from './modalWithForm.module.css';
import { MaskedInput } from 'antd-mask-input';

interface ModalWithFormProps {
  isVisible: boolean;
  handleCancel: () => void;
  handleSubmit: () => void;
  title: string;
  form: FormInstance;
}

const ModalWithForm: React.FC<ModalWithFormProps> = (props): JSX.Element => {

  const { isVisible, handleCancel, handleSubmit, title, form } = props;

  return (
    <Modal
      className={styles.modal}
      title={title}
      visible={isVisible}
      onCancel={handleCancel}
      width="40%"
      footer={[<Button key="submit" className={styles.button} htmlType="submit" onClick={handleSubmit}></Button>]}>
      <Form form={form} autoComplete="off">
        <Form.Item
          name="avatar"
          label=""
          className={styles.formElement}
          rules={[{ type: 'url', message: '   ' }]}>
          <Input placeholder='  ' className={styles.input} />
        </Form.Item>
        <Form.Item
          name="name"
          label=""
          className={styles.formElement}
          rules={[{ required: true, message: ',  !' }, { type: 'string', warningOnly: true }]}>
          <Input placeholder=' ' className={styles.input} />
        </Form.Item>
        <Form.Item
          name="phone"
          label=" "
          className={styles.formElement}
          rules={[{ required: true, message: ',   !' }]}>
          <MaskedInput className={styles.input} mask='8-(900)-000-00-00' />
        </Form.Item>
        <Form.Item
          name="email"
          label=""
          className={styles.formElement}
          rules={[{ required: true, message: ',  !' }, { type: 'email', warningOnly: true, message: "     example@example.ru" }]}>
          <Input placeholder=' ' className={styles.input} />
        </Form.Item>
      </Form>
    </Modal>
  )
}

export default ModalWithForm;